import uniCompusButton from '@/components/uni-compus-components/unicompus-button.vue';
import avatar from '@/components/uni-compus-components/uniCompus-avatar.vue';
import uniBadge from '@/components/uni-badge/uni-badge.vue';
import lvSelect from '@/components/lv-select/lv-select.vue';
import KpAvatar from '@/components/kp-avatar/index.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import { pathToBase64, base64ToPath } from '@/js_sdk/gsq-image-tools/image-tools/index.js'
import { mapMutations, mapState } from 'vuex';
import {activityInoValidator} from '@/utils/validator.js'
export default {
	computed: {
		...mapState(['group'])
	},
	data() {
		return {
			deadLine:"",
			showErr:false,
			errData:{title:'提示',content:'这是一个模态弹窗',cancelText:'取消',confirmColor:'#3CC51F'},
			content:"",
			info: {type:100},
			categoryList: [{category:'舞蹈',en:"dance"}, {category:'绘画',en:"draw"},
			{category:'编程',en:"programming"},{category:'文学',en:"literature"},{category:'英语',en:"English"} ],
			choosedCategory: '',
			scrollHeight: 500,
			userChoosedGroup: {},
			groupID: '',
			description: '',
			isGroup: false,
			needTitle: false,
			tapIndex: 0,
			showGroupList: false,
			groupActive: 0,
			isSubmit: true,
			ImgShow: false,
			title: '',
			groupList: [],
			videoSrc: '',
			publishImgList: [],
			iconList: [
				{ imgSrc: '../../static/index/MyActivity.png', content: '活动' },
				{ imgSrc: '../../static/index/activity.png', content: '动态' },
				// { imgSrc: '../../static/index/answer.png', content: '问答' },
				{ imgSrc: '../../static/index/knowledge.png', content: '知识' }
			],

			showValue: 'name', // 需要显示的数据，必须与infoList中的name对应
			searchValue: '',
			infoList: [],
			infoLists: [
				{
					name: '吕星辰1'
				},
				{
					name: '吕星辰2'
				},
				{
					name: '吕星辰3'
				},
				{
					name: '吕星辰4'
				}
			]
		};
	},
	methods: {
		...mapMutations({
			addActivity: 'ADD_ACTIVITYINFO'
		}),
		
		chooseDeadLine(){
			this.$refs.shortTerm.show()
		},
		onConfirmDeadLine(event){

			this.deadLine = event.result
		},
		
		async sendActivity() {
			this.info.title = this.title;
			this.info.description = this.description;
			
			this.info.groupId = this.groupID;
			this.info.category = this.choosedCategory
			this.info.content = this.content
			
			console.log(this.info)
			let errMsg = activityInoValidator(this.info,this.publishImgList)
			
			if(!errMsg){
				uni.showModal({
					title:"确认提交",
					content:"请确认信息无误，审核后即将发表",
					success:async result=>{
						let activityInfo = await this.request('v1/ActivityInfo/upLoadActivity',this.info,'POST')
						activityInfo = activityInfo[1].data
						console.log(activityInfo)
						this.publishImgList.forEach(item=>{
							this.uploadFile('v1/uploadFiles/files',item,{activity_id:activityInfo.activity_id,type:activityInfo.type})
						})
						if(this.videoSrc){
							this.uploadFile('v1/uploadFiles/files',this.videoSrc,{activity_id:activityInfo.activity_id,type:activityInfo.type})
				
						}
						uni.showToast({
							title:"提交成功",
							duration:2500,
							success: () => {
								uni.switchTab({
									url:"/pages/index/index"
								})
							}
						})
					}
				})
				
			}else{
				let obj = this.errData
				obj.content = errMsg
				this.errData = obj
				this.showErr = true
			}
			
		},
		chooseCategory() {
			this.$refs.CategoryPopup.open();
		},
		finishChooseCategory(item) {
			
			this.choosedCategory = item.category;
			this.info.category = item.en
			this.$refs.CategoryPopup.close();
		},
		chooseGroup(id, group) {
			this.userChoosedGroup = group;
			this.groupID = id;
			this.$refs.popup.close();
		},
		handleSearch() {
			this.loading = true;
			setTimeout(() => {
				this.loading = false;
				this.infoList = this.infoLists;
			}, 2000);
		},
		change(val) {
			console.log(val);
		},
		cancelChoose(index) {
			this.publishImgList.splice(index, 1);
		},
		isTitle(e) {
			this.needTitle = e.detail.value;
		},
		chooseType(index) {
			if (index == 0) {
				this.info.type = 100;
			} else if (index == 1) {
				this.info.type = 200;
			} else if (index == 2) {
				this.info.type = 400;
			}

			this.tapIndex = index;
		},
		clickLeft() {
			uni.navigateBack({
				animationDuration: 300,
				animationType: 'pop-out'
			});
		},
		showGroup(e) {
			let needGroup = e.target.value;
			if (needGroup) {
				this.$refs.popup.open();
			} else {
				this.groupID = '';
				this.userChoosedGroup = {};
			}
		},
		chooseImg() {
			let self = this;
			uni.chooseImage({
				count: 9, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: function(res) {
					console.log(res);
					self.publishImgList = res.tempFilePaths;
					console.log(self.publishImgList);
				}
			});
		},
		chooseVideo() {
			let self = this;
			uni.chooseVideo({
				count: 1,
				sourceType: ['camera', 'album'],
				success: function(res) {
					self.videoSrc = res.tempFilePath;
					console.log(self.videoSrc);
				}
			});
		}
	},
	async created() {
		let groupList = await this.request('groups/');
		this.groupList = groupList[1].data.data.records;
		console.log(this.group);
	},
	components: {
		uniCompusButton,
		avatar,
		uniBadge,
		KpAvatar,
		uniPopup,
		lvSelect
	}
};
